@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PTMono Regular";
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto Medium";
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed Regular";
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed Bold";
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rouble";
  font-display: swap;
  src: url("../fonts/rouble.woff2") format("woff2"), url("../fonts/rouble.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
  border: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.is-hidden {
  display: none;
}

.container {
  max-width: 1220px;
  margin: 0 auto;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.button {
  font-family: "Roboto Condensed Bold", "Arial", sans-serif;
  font-weight: 700;
  border: 2px solid #00b2ff;
  border-radius: 3px;
  color: #e5e5e5;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal--nojs {
  opacity: 1;
  visibility: visible;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 145px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 100%;
  padding: 40px;
  border-radius: 3px;
  background-color: #00b2ff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 17px;
  right: 17px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
  fill: #ffffff;
}
@media (hover: hover) {
  .modal__close-btn:hover:not(.focus-visible) {
    fill: #ffd801;
  }
}
.modal__close-btn.focus-visible:focus {
  fill: #ffd801;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: 500px;
  position: fixed;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  position: fixed;
  width: 100%;
  top: 0;
  padding: 28px 0 35px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  z-index: 2;
}
@media (max-width: 1023px) {
  .header {
    position: absolute;
    padding: 20px 0 25px;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 15px 0 20px;
  }
}

@media (max-width: 1023px) {
  .header__menu {
    padding: 0 50px 0;
  }
}
@media (max-width: 767px) {
  .header__menu {
    padding: 0 25px 0;
  }
}

.menu {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 0 60px 0 0;
}

.menu__contacts {
  width: 460px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
}
@media (max-width: 1023px) {
  .menu__contacts {
    padding: 0 30px 10px 0;
    width: 110px;
    margin-top: 10px;
  }
}
@media (max-width: 767px) {
  .menu__contacts {
    width: 100px;
    margin: 0;
    padding: 0 20px 4px 0;
  }
}

.menu__text {
  font-family: "PTMono Regular", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  color: #f2f5f6;
  padding-left: 15px;
}
@media (hover: hover) {
  .menu__text:hover:not(.focus-visible) {
    color: #ffd801;
  }
}
.menu__text.focus-visible:focus {
  color: #ffd801;
}
@media (max-width: 1023px) {
  .menu__text {
    display: none;
  }
}

.menu__button {
  width: 160px;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
  border: 1px solid #f2f5f6;
  padding: 10px 15px;
  background-color: transparent;
  cursor: pointer;
  letter-spacing: 0.05em;
  word-break: break-word;
  margin-top: 7px;
}
@media (hover: hover) {
  .menu__button:hover:not(.focus-visible) {
    color: #ffd801;
  }
}
.menu__button.focus-visible:focus {
  color: #ffd801;
}
@media (max-width: 1023px) {
  .menu__button {
    display: none;
  }
}

.menu__email-icon,
.menu__phone-icon {
  width: 20px;
  height: 20px;
  fill: #00b2ff;
}
@media (hover: hover) {
  .menu__email-icon:hover:not(.focus-visible),
.menu__phone-icon:hover:not(.focus-visible) {
    fill: #ffd801;
  }
}
.menu__email-icon.focus-visible:focus,
.menu__phone-icon.focus-visible:focus {
  fill: #ffd801;
}

.menu__logo {
  width: 215px;
  display: flex;
  align-items: center;
}
@media (max-width: 767px) {
  .menu__logo {
    width: 35px;
  }
}

.menu__logo img {
  width: 100%;
}

.promo {
  background: url("../img/promo/promo-bg.jpg") center center/cover no-repeat;
  background-color: #041827;
  padding-bottom: 280px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .promo {
    background-image: url("../img/promo/promo-bg@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .promo {
    padding-bottom: 207px;
    margin-bottom: 0;
    background-position: -123px 0;
  }
}
@media (max-width: 767px) {
  .promo {
    padding-bottom: 190px;
    margin: 0;
    background-position: -228px 0;
  }
}

.promo__content-wrapper {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  padding: 305px 0 0 125px;
}
@media (max-width: 1023px) {
  .promo__content-wrapper {
    padding: 180px 0 0 0;
  }
}
@media (max-width: 767px) {
  .promo__content-wrapper {
    padding: 150px 0 0 0;
  }
}

.promo__content-wrapper h1 {
  font-family: "Roboto Condensed Bold", "Arial", sans-serif;
  font-size: 80px;
  font-weight: 700;
  line-height: 94px;
  color: #f2f5f6;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  margin: 0;
  padding-bottom: 32px;
}
@media (max-width: 1023px) {
  .promo__content-wrapper h1 {
    font-size: 64px;
    line-height: 75px;
    padding-bottom: 0;
  }
}
@media (max-width: 767px) {
  .promo__content-wrapper h1 {
    font-size: 36px;
    line-height: 42px;
    padding-bottom: 8px;
  }
}

.promo__content-wrapper p {
  font-family: "PTMono Regular", "Arial", sans-serif;
  font-size: 22px;
  font-weight: 400;
  line-height: 34px;
  color: #f2f5f6;
  margin: 0;
  padding-bottom: 47px;
}
@media (max-width: 1023px) {
  .promo__content-wrapper p {
    padding-bottom: 58px;
  }
}
@media (max-width: 767px) {
  .promo__content-wrapper p {
    font-size: 14px;
    line-height: 21px;
    padding-bottom: 45px;
  }
  .promo__content-wrapper p br {
    display: none;
  }
}

.promo__button {
  width: 410px;
  font-size: 18px;
  font-weight: 700;
  line-height: 21px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  padding: 16px 25px;
  background-color: #00b2ff;
  border: none;
  cursor: pointer;
}
.promo__button--nojs {
  display: none;
}
@media (hover: hover) {
  .promo__button:hover:not(.focus-visible) {
    color: #ffd801;
    background-color: #041827;
  }
}
.promo__button.focus-visible:focus {
  color: #ffd801;
  background-color: #041827;
}
.promo__button--mobile {
  display: none;
}
@media (max-width: 1023px) {
  .promo__button {
    margin-left: 46px;
  }
}
@media (max-width: 767px) {
  .promo__button {
    width: 270px;
    display: none;
    margin: 0;
    font-size: 14px;
    line-height: 16px;
    padding: 12px 25px;
  }
  .promo__button--mobile {
    display: block;
  }
}

.features {
  background-color: #ffffff;
  margin: 0;
  margin-top: 120px;
}
@media (max-width: 1023px) {
  .features {
    margin-top: 80px;
  }
}
@media (max-width: 767px) {
  .features {
    margin-top: 50px;
  }
}

.features__list {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, minmax(595px, 1fr));
  grid-gap: 30px;
  list-style: none;
}
@media (max-width: 1439px) {
  .features__list {
    grid-template-columns: repeat(2, minmax(320px, 1fr));
    grid-gap: 30px;
  }
}
@media (max-width: 767px) {
  .features__list {
    grid-template-columns: minmax(270px, 1fr);
    grid-gap: 30px;
  }
}

.features__item {
  min-height: 300px;
  margin: 0;
  padding: 40px 19px 20px 43px;
  border: 1px solid #e5e5e5;
  border-left: 5px solid #00b2ff;
  cursor: default;
}
@media (max-width: 1023px) {
  .features__item {
    min-height: 360px;
    padding: 40px 36px 28px 40px;
  }
}
@media (max-width: 767px) {
  .features__item {
    min-height: 340px;
    padding: 31px 30px 0 25px;
  }
}

.features__item-icon {
  width: 35px;
  height: 35px;
  fill: #00b2ff;
}
@media (max-width: 767px) {
  .features__item-icon {
    width: 30px;
    height: 30px;
  }
}

.features__item h3 {
  font-family: "Roboto Condensed Regular", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 34px;
  color: #00b2ff;
  margin: 0;
  padding: 14px 0 14px;
}
@media (max-width: 1023px) {
  .features__item h3 {
    padding: 7px 0 8px;
  }
}
@media (max-width: 767px) {
  .features__item h3 {
    font-size: 20px;
    line-height: 28px;
    padding: 9px 0 9px;
  }
}

.features__item p {
  font-family: "PTMono Regular", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #041827;
  margin: 0;
}
@media (max-width: 767px) {
  .features__item p {
    font-size: 14px;
    line-height: 19px;
  }
}

.features__item:hover {
  background-color: #00b2ff;
}
.features__item:hover * {
  color: #ffffff;
}
.features__item:hover svg {
  fill: #ffffff;
}

@media (max-width: 767px) {
  .about {
    margin: 48px 0 80px;
  }
}

.about__container {
