.products {
  background-color: $color-black-pearl;
  margin-top: 97px;
  padding-bottom: 140px;

  @media (max-width: 1023px) {
    margin-top: 77px;
    padding-bottom: 0;
  }

  @media (max-width: 767px) {
    padding: 47px 0 86px;
    margin-top: 0;
  }
}

.products__title {
  font: {
    family: $roboto-condensed-regular;
    size: 36px;
    weight: 400;
  }
  line-height: 50px;
  color: $color-default-white;
  margin: 0;
  padding-top: 68px;

  @media (max-width: 1023px) {
    padding-top: 55px;
  }

  @media (max-width: 767px) {
    font-size: 28px;
    line-height: 39px;
    padding: 0 35px 0 0;
  }

  &--desktop {
    @media (max-width: 767px) {
      display: none;
      font-size: 28px;
      line-height: 40px;
    }
  }

  &--mobile {
    display: none;

    @media (max-width: 767px) {
      display: block;
    }
  }
}

.products__list {
  margin: 53px 0 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, 350px);
  grid-column-gap: 85px;
  grid-row-gap: 30px;

  @media (max-width: 1439px) {
    margin-top: 40px;
    padding-bottom: 75px;
    grid-template-columns: minmax(670px, 1fr);
  }

  @media (max-width: 767px) {
    grid-template-columns: minmax(270px, 1fr);
    margin: 28px 0 0;
    padding-bottom: 0;
  }
}

.products__content-wrapper {
  position: relative;
  z-index: 1;
}

.products__img-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.products__img-wrapper img {
  @media (max-width: 1439px) {
    width: 100%;
    height: 250px;
    object-fit: cover;
  }
}

.products__item {
  position: relative;
  min-height: 650px;
  cursor: pointer;
  background-color: $color-black-pearl;

  @media (max-width: 1439px) {
    min-height: 250px;
  }
}

.products__heading {
  font: {
    family: $roboto-condensed-regular;
    size: 20px;
    weight: 400;
  }
  line-height: 30px;
  color: $color-default-white;
  padding: 47px 137px 0 40px;
  margin: 0;

  @media (max-width: 1023px) {
    font-size: 20px;
    line-height: 30px;
    padding: 40px 475px 0 45px;
  }

  @media (max-width: 767px) {
    font-size: 18px;
    line-height: 30px;
    padding: 25px 105px 0 30px;
  }
}

.products__btn {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 60px;
  height: 60px;
  background-color: $color-deep-sky-blue;
  border-radius: 3px;
  cursor: pointer;

  @media (max-width: 767px) {
    width: 40px;
    height: 40px;
  }
}

.products__arrow-wrapper {
  position: relative;
  width: 60px;
  height: 60px;

  @media (max-width: 767px) {
    width: 40px;
    height: 40px;
  }
}

.products__arrow {
  position: absolute;
  width: 10px;
  height: 10px;
  fill: $color-default-white;
  top: 40%;
  right: 40%;
}
